<template>
<v-app>
  <v-card
  class="mx-auto my-auto"
  cols="12" sm="6" md="4">
  <v-container cols="12" sm="6" md="4">
    <img src="../assets/web-tr.jpg" alt="" width="100%">
  </v-container>

  <h4 class="text-center"> WELCOME </h4>
  <p class="text-center"> Sign to continue to TR </p>
  <v-divider class="mx-4"></v-divider>
  <br>

  <v-container cols="12" sm="6" md="4">
    <v-row>
      <v-col cols="12">
        <p> Username </p>
        <v-text-field
        label="Enter username"
        v-model="user.username"
        outlined
        dense
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <p> Password </p>
        <v-text-field
        label="Enter password"
        v-model="user.password"
        type="password"
        outlined
        dense
        ></v-text-field>
      </v-col>
    </v-row>
    <br>
    <v-btn block
    @click="iniciarSesion()"
    depressed
    color="#E47911"
    class="white--text"
    cols="12" sm="6" md="4"
    :disabled="camposVacios"
    :loading="loading"
    > LOG IN
    <v-icon
    right
    dark
    > mdi-login </v-icon>
    </v-btn>
    </v-container>
    <br>
    <p class="subtitle-2 text-center" @click="forgot = true"> Forgot your password? </p>

    <v-dialog
        v-model="forgot"
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            Enter your email to recover your account.
          </v-card-title>
          <v-text-field
            class="ma-2"
            placeholder="Email"
            required
            outlined
            dense
          ></v-text-field>
          <v-divider></v-divider>
          <v-btn color="#E47911" dark class="ma-2" small>
            Send request
          </v-btn>
        </v-card>
      </v-dialog>
  </v-card>
  <firma></firma>
</v-app>
</template>

<script>
  import Firma from '@/components/Title';

  export default {
    components: {
      Firma,
    },

    data: () => ({
      loading: false,
      user: {
        username: "",
        password: "",
      },
      forgot : false,
    }),
    computed: {
      /* ESTE ES UN METODO QUE SIEMPRE ESTA ACTIVO, VERIFICANDO QUE LOS 
          CAMPOS ESTAN VACIOS O NO */
      camposVacios() {
          if (this.user.username && this.user.password)
              return false;

          return true;
      },

      /* ESTE ES UN METODO QUE SIEMPRE ESTA ACTIVO, 
          QUE RETORNA EL ESTATUS DEL USUARIO */
      loggedIn() {
          return this.$store.state.auth.status.loggedIn;
      }
  },

    created() {
      /* SI EL USUARIO ESTA ACTIVO LO ENVIA A LA PANTALLA DE DASHBOARD */
      if (this.loggedIn){
          this.$router.push('/dashboard');
      }
    },

    methods: {
      iniciarSesion (){
        this.loading = true;
        this.$store.dispatch('auth/login', this.user)
            .then(() => {
                this.loading = false;
                location.reload();
            });
      }
    }
  }
</script>